<template>
<div>
    <v-content>
        <v-container>
            <v-card>
            <v-img src="https://cdn.vuetifyjs.com/images/parallax/material2.jpg" :aspect-ratio="25/9" width="100%"> 
                <!-- <div class="fill-height bottom-gradient"></div> -->
                <v-btn class="title ticket_value" color="orange" dark width="150px" height="50px">$2500</v-btn>
                                
             </v-img>
            <v-card-actions class="mt-3 mb-10">
                <svg width="40px" height="60px">
                    <rect width="10" height="60" style="fill:rgb(255, 153, 0)"></rect>
                </svg>
                <span class="headline font-weight-bold mt-10 mb-10">Limbo Pop VS La Final: Rihanna VS Talor Swift</span>
                <v-spacer></v-spacer>
                <button style="background-color:grey;">
                    <div><i class="fa fa-calendar fa-3x" style="color:#3d0f66; margin-top:10px"></i></div>
                    <div class="black--text ml-5 mr-5">Sabado 17/08<br>23:30</div>
                </button>
                <button style="background-color:grey; margin-left:20px">
                    <div><i class="fa fa-map-marker fa-3x" style="color:#3d0f66; margin-top:10px"></i></div>
                    <div class="black--text ml-5 mr-5">Club Paraguay<br>Pje.Agustin Paez 99, Cordoba</div>
                </button>
            </v-card-actions>
            </v-card>
            <v-row>
                <v-col md="6">
                <div>
                    Flux is not a framework, architecture or language. Its a concept. When we use Flux in Vue (or any
other language/framework) we create an unique way to update data that ensures every thing is
right. This way to update data also ensures that the view components also keep the right state. This
is really important to complex SPAs (Single Page Applications), that has plenty tables and screens,
and of course events and data changes all the time.
                </div>
                </v-col>
                <v-spacer></v-spacer>
                <v-col md="3"><v-btn class="white--text addcart mt-5" block color="orange">ADD ONE TO THE CART</v-btn></v-col>
            </v-row>
            
            <v-row class="mt-10">
                <GmapMap
                    :center="{lat:10, lng:10}"
                    :zoom="7"
                    map-type-id="terrain"
                    style="width: 100%; height: 500px"
                    >
                    <GmapMarker
                        :key="index"
                        v-for="(m, index) in markers"
                        :position="m.position"
                        :clickable="true"
                        :draggable="true"
                        @click="center=m.position"
                    />
                </GmapMap>
            </v-row>
            
        </v-container>
    </v-content>
</div>    
</template>
<script>
// import GmapMap from 'vue2-google-maps'
// import GmapMarker from 'vue2-google-maps'
export default {
    name: "TicketDetail",
    components: {
        // 'GmapMap': GmapMap,
        // 'GmapMarker': GmapMarker
    },
    data() {
        return {
        // default to Montreal to keep it simple
        // change this to whatever makes sense
        center: { lat: 10.0, lng: 10.0 },
        markers: [
            {
                position: { lat: 10.0, lng: 10.0 }
            },
            {
                position: { lat: 11.0, lng: 11.0 }
            },
        ],
        places: [],
        
        };
    },
    mounted() {
    this.geolocate();
    },
    methods: {
    // receives a place object via the autocomplete component
    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    }
  }
    
}
</script>
<style lang="scss" scoped>
.addcart {
    margin-top: -40px;
    margin-right: 20px;
}
.ticket_value {
    margin-top: 30%;
}
</style>


