<template>
    <div id="homepage">
        <!-- <Header></Header> -->
        <SearchHome></SearchHome>
        <TicketsList></TicketsList>
        <!-- <EventsList></EventsList> -->
        <ProduceEvent></ProduceEvent>
        <Footer></Footer>
    </div>
</template>
<script>
import Header from './Header'
import SearchHome from './SearchHome'
import Footer from './Footer'
import EventsList from './EventsList'
import TicketsList from './TicketsList'
import ProduceEvent from './ProduceEvent'
export default {
    name: "Home",
    components: {
        "Header": Header,
        "SearchHome": SearchHome,
        "EventsList": EventsList,
        "Footer": Footer,
        "TicketsList": TicketsList,
        "ProduceEvent": ProduceEvent,
    },
    computed: {
        tickets() {
            return this.$store.getters.getNoteBooks;
        }
    },
    data() {
        return {
            search: '',
            namemode: [''],
            provincemode: [''],
            monthmode: [''],
            citymode: [''],
        }
    }
}
</script>

<style lang="scss">
@import '../styles/home.scss'
</style>


