<template>
    <v-content>
        <v-row justify="center" id="profileheadline">
            <div>
                <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">Edit Profile</h1>
            </div>
        </v-row>
        <v-container>
            <v-row justify="center">
                <v-col md="6" id="profilepage" class="mb-10 mt-10">
                    <v-form ref="form" style="margin-top: 5%;" v-model="valid" lazy-validation>
                        <v-text-field 
                        class="white--text ml-5 mr-5"
                        v-model="email"
                        prepend-inner-icon="mdi-email" 
                        background-color="white" 
                        :rules="emailRules" 
                        label="Tu Email"
                        single-line 
                        outlined required
                        ></v-text-field>
                        <v-row>
                            <v-col md="6">
                                <v-text-field 
                                class="white--text ml-5"
                                v-model="name"
                                prepend-inner-icon="mdi-account" 
                                background-color="white" 
                                :counter="10"
                                :rules="nameRules" 
                                label="Nombre"
                                single-line
                                outlined 
                                required
                                ></v-text-field>
                            </v-col>
                            <v-col md="6">
                                <v-text-field 
                                prepend-inner-icon="mdi-account" 
                                v-model="last_name"
                                class="mr-5" 
                                background-color="white" 
                                :counter="10" 
                                :rules="nameRules" 
                                label="Apellido" 
                                single-line 
                                outlined
                                required
                                ></v-text-field>
                            </v-col>
                        </v-row>
                        <v-text-field 
                        prepend-inner-icon="mdi-phone" 
                        v-model="phone" 
                        class="ml-5 mr-5"
                        background-color="white"
                        type="number" 
                        :rules="phoneRules" 
                        label="Telfono" 
                        min="0"
                        single-line
                        outlined 
                        required
                        ></v-text-field>
                        <v-text-field 
                            prepend-inner-icon="mdi-key-variant"
                            v-model="dni"
                            background-color="white"
                            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" 
                            :rules="[passwordRules.required, passwordRules.min]"
                            :type="show ? 'text' : 'password'"
                            label="DNI"
                            hint="At least 8 characters"
                            value=""
                            class="input-group--focused ml-5 mr-5"
                            @click:append="show = !show"
                            single-line
                            outlined
                        ></v-text-field>
                        <v-text-field 
                            prepend-inner-icon="mdi-key-variant"
                            v-model="pasaporte"
                            background-color="white"
                            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" 
                            :rules="[passwordRules.required, passwordRules.min]"
                            :type="show ? 'text' : 'password'"
                            label="Pasaporte"
                            hint="At least 8 characters"
                            value=""
                            class="input-group--focused ml-5 mr-5"
                            @click:append="show = !show"
                            single-line
                            outlined
                        ></v-text-field>
                        <!-- <v-row>
                            <v-col md="6">
                            <v-text-field 
                            class="white--text ml-5"
                            v-model="name"
                            background-color="white"
                            label=""
                            single-line
                            outlined
                        ></v-text-field>
                        </v-col>
                        </v-row> -->
                        
                        
                        <!-- <v-text-field
                            v-model="confirm_password"
                            type="password"
                            v-validate="'required|confirmed:password'"
                            :class="{'is-danger': errors.has('confirm_password')}"
                            :error-messages="errors.first('confirm_password')"
                            label="Confirm the password"
                            data-vv-name="password"
                            required
                        ></v-text-field> -->
                        <!-- <v-row>
                            <v-checkbox v-model="checkbox" color="black" class="ml-8">
                                <template v-slot:label>
                                    <div style="color:black">
                                    I have read the 
                                    <v-tooltip bottom>
                                        <template v-slot:activator="{ on }">
                                        <a href="#" @click.stop v-on="on">
                                            <font style="color:black">terms and conditions.</font>
                                        </a>
                                        </template>
                                        Open terms and conditions
                                    </v-tooltip>
                                    </div>
                                </template>
                            </v-checkbox>
                        </v-row> -->
                        <div style="text-align:center">
                            <v-btn class="white--text mb-10" block color="orange" height="50px" @click="$router.push('/event/create')"><font style="font-size:1.5rem">Edit Profile</font></v-btn>
                        </div>
                    </v-form>
                </v-col>
            </v-row>
        </v-container>
    </v-content>
</template>
<script>
export default {
    name: 'ProfileForm',
    components: {

    },
    $_veeValidate: {
        validator: 'new'
    },
    data() {
        return {
            valid: true,
            name: '',
            last_name: '',
            nameRules: [
                v => !!v || 'Name is required',
                v => (v && v.length <= 10) || 'Name must be less than 10 characters',
            ],
            email: "",
            emailRules: [
                v => !!v || "E-mail is required",
                v => /.+@.+\..+/.test(v) || "E-mail must be valid"
            ],
            phone: '',
            phoneRules: [
                v => !!v || "Phone number is required"
            ],
            dni: '',
            pasaporte: '',
            show: true,
            passwordRules: {
            required: value => !!value || 'Required.',
            min: v => v.length >= 8 || 'Min 8 characters',
            },
            // confirm_password: '',
            checkbox: false,
            terms: false,

        }
    },
    methods: {
        submit() {
            this.$validator.validateAll()
                .then(result => {
                    console.log(result)
                })
        }
    }
}
</script>
<style lang="scss" scoped>
#profilepage {
    background-color: #e8edea;
    background-size: 100%, 100%;
}
#profileheadline {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}
</style>


