<template>
<div>
    <Header></Header>
    <v-content id="cartcheck">
        <v-container>
            <h1 style="text-align:center; margin-top:1rem">Tickets in Cart</h1>
            <div v-if="cartnumber == 0">
                <v-row justify="center">
                    <h3 style="margin-top: 20%">There are no any tickets in your cart.</h3>
                </v-row>
                <v-row></v-row>
            </div>
        </v-container>
        <v-container>
            <v-row>
                <div v-for="(ticket, index) in tickets" :key="index">
                    <v-hover v-slot:default="{ hover }">
                        <v-card class="ma-5" color="grey lighten-4" max-width="350" shaped>
                            <v-img :aspect-ratio="16/9" :src="ticket.image">
                                <v-expand-transition>
                                    <!-- <div v-if="hover" class="d-flex transition-fast-in-fast-out orange darken-2 v-card--reveal display-3 white--text" style="height: 100%;">$14.99</div> -->
                                </v-expand-transition>
                            </v-img>
                            <v-card-text class="pt-6" style="position: relative;">
                                <!-- <div class="font-weight-light grey--text title mb-2">Status:{{ tickets. }}</div> -->
                                <div class="headline black--text title mb-2">{{ ticket.name }}</div>
                                <div class="font-weight-light grey--text title mb-2">From {{ ticket.startdate + ", " + ticket.starttime}}</div>
                                <div class="font-weight-light grey--text title mb-2">To {{ ticket.enddate + ", " + ticket.endtime}}</div>
                                <div class="font-weight-light grey--text title mb-2">{{ ticket.price }}</div>
                                <div class="font-weight-light grey--text title mb-2">{{ ticket.place }}</div>
                                <v-btn class="white--text headline" block color="orange" height="40">Buy Ticket</v-btn>
                                <v-btn class="mt-2 white--text headline" block color="orange" height="40" @click="remove(index)">Delete Ticket</v-btn>
                            </v-card-text>
                        </v-card>
                    </v-hover>
                </div>
            </v-row>
            <v-row>
                <v-spacer></v-spacer>
                <v-col md="3">
                    <div v-show="cartnumber != 0">
                        <v-btn class="white--text subtitle-1 mt-10" color="orange">Finish and Checkout</v-btn>
                    </div>
                </v-col>
            </v-row>
        </v-container>
    </v-content>
    </div>
</template>
<script>
import Header from './Header'
export default {
    name: "CartCheckout",
    components: {
        "Header": Header
    },
    computed: {
        tickets() {
            // console.log(this.$store.state.cartTickets)
            return this.$store.getters.getTicketInCart;
        },
        cartnumber() {
            return this.$store.getters.getNumberInCart
        }
    },
    data() {
        return {
            hover: true,
            // tickets:this.$store.getters.getTicketInCart
        }
    },

    methods: {
        remove(index) {
            this.$store.commit('REMOVE_TICKET', index);
        }
    }
}
</script>
<style lang="scss" scoped>
#cartcheck {
    background-image: -webkit-linear-gradient( -29deg, rgb(193, 15, 65) 0%, rgb(35, 59, 136) 100%);
    background-size: 100%, 100%;
}
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .5;
  position: absolute;
  width: 100%;
}
</style>


