<template>
    <v-content id="resetpassword">
        <v-container>
            <v-row justify="center" style="margin-top: 10%;">
                <h1>RADICALTICKETS</h1>
            </v-row>
            <div style="text-align:center; color:white">
                <h2>Administrative Intranet</h2><br>
                <h3>Ingrese su nueva clave</h3>
            </div>
            <v-row justify="center" class="mt-10">
                <v-col md="6">
                    <v-form ref="form" v-model="valid" lazy-validation>
                        <v-text-field
                        prepend-inner-icon="mdi-key-variant"
                        v-model="password"
                        :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[passwordRules.required, passwordRules.min]"
                        :type="show ? 'text' : 'password'"
                        label="Contrasea"
                        hint="At least 8 characters"
                        value=""
                        background-color="white"
                        outlined
                        single-line
                        @click:append="show = !show" 
                        ></v-text-field>
                        <v-btn class="white--text" color="#bf095d" block height="50px" @click="$router.push('/#')">
                            <font style="font-size:1.5rem">ODIFICAR CONTRASEA</font>
                        </v-btn>
                        <v-row justify="center" class="mt-10">
                            <router-link to="/user/forgetpass">
                                <font style="font-size:1rem+; color:white">Regresar al ingreso.</font>
                            </router-link>
                        </v-row>
                    </v-form>
                </v-col>    
            </v-row>
        </v-container>
    </v-content>   
</template>
<script>
export default {
    name: 'ResetPassword',
    data() {
        return {
            valid: true,
            password: '',
            show: true,
            passwordRules: {
            required: value => !!value || 'Password required.',
            min: v => v.length >= 8 || 'Min 8 characters',
            },
        }
    }
}
</script>
<style lang="scss" scoped>
#resetpassword {
    background-image: -webkit-linear-gradient( -29deg, rgb(193, 15, 65) 0%, rgb(35, 59, 136) 100%);
    background-size: 100%, 100%;
}
</style>


