import Vue from 'vue'
import Router from 'vue-router'
import EventCreate from '../components/EventCreate.vue'
import EventCreateLogged from '../components/EventCreateLogged.vue'
import Login from '../components/Login.vue'
import RegisterPage from '../components/RegisterPage.vue'
// import RegisterForm from '../components/RegisterForm.vue'
import Home from '../components/Home.vue'
import Footer from '../components/Footer.vue'
// import Header from '../components/Header.vue'
import ProducerPage from '../components/ProducerPage.vue'
import ForgetPassword from '../components/ForgetPassword.vue'
import ResetPassword from '../components/ResetPassword.vue'
import EventsList from '../components/EventsList.vue'
import TicketList from '../components/TicketsList.vue'
import CartCheck from '../components/CartCheckout.vue'
import SaleResume from '../components/SaleResume.vue'
import ControlPanelPage from '../components/ControlPanelPage.vue'
import ProfileEditPage from '../components/ProfileEditPage.vue'
import MyTicketPage from '../components/MyTicketPage.vue'
import MyEventPage from '../components/MyEventPage.vue'
import EditEventPage from '../components/EditEventPage.vue'
import MyJrpPage from '../components/MyJrpPage.vue'
import SendInvitation from '../components/SendInvitation.vue'
import CartPage from '../components/CartPage.vue'
import TicketDetailPage from '../components/TicketDetailPage.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    // {
    //   path: '/header',
    //   name: 'Header',
    //   component: Header
    // },
    {
      path: '/user/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/user/register/',
      name: 'RegisterPage',
      component: RegisterPage
    },
    // {
    //   path: '/registerform',
    //   name: 'RegisterForm',
    //   component: RegisterForm
    // },
    {
      path: '/panel',
      name: 'ControlPanelPage',
      component: ControlPanelPage
    },
    {
      path: '/editprofile',
      name: 'ProfileEditPage',
      component: ProfileEditPage
    },
    {
      path: '/ticket/cart',
      name: 'CartPage',
      component: CartPage
    },
    {
      path: '/ticket/myticket',
      name: 'MyTicketPage',
      component: MyTicketPage
    },
    {
      path: '/event/myevent',
      name: 'MyEventPage',
      component: MyEventPage
    },
    {
      path: '/editevent',
      name: 'EditEventPage',
      component: EditEventPage
    },
    {
      path: '/myjrp',
      name: 'MyJrpPage',
      component: MyJrpPage
    },
    {
      path: '/event/createlogged',
      name: 'EventCreateLogged',
      component: EventCreateLogged
    },
    {
      path: '/producer',
      name: 'ProducerPage',
      component: ProducerPage
    },
    {
      path: '/user/resetpass',
      name: 'ResetPassword',
      component: ResetPassword
    },
    {
      path: '/user/forgetpass',
      name: 'ForgetPassword',
      component: ForgetPassword
    },
    {
      path: '/event/create',
      name: 'EventCreate',
      component: EventCreate
    },
    // {
    //   path: '/event/list',
    //   name: 'EventsList',
    //   component: EventsList
    // },
    // {
    //   path: '/ticket/list',
    //   name: 'TicketsList',
    //   component: TicketList
    // },
    {
      path: '/ticket/check',
      name: 'CartCheck',
      component: CartCheck
    },
    {
      path: '/saleresume',
      name: 'SaleResume',
      component: SaleResume
    },
    {
      path: '/invitation',
      name: 'SendInvitation',
      component: SendInvitation
    },
    {
      path: '/ticket/detail',
      name: 'TicketDetailPage',
      component: TicketDetailPage
    },
  ]
})



