    getters: {
        getNoteBooks: state => {
            var getNoteBooks = state.notebooks.map(notebook => {
                return {
                    name: '*' + notebook.name + '*',
                    startdate: notebook.startdate,
                    starttime: notebook.starttime,
                    enddate: notebook.enddate,
                    endtime: notebook.endtime,
                    image: notebook.image,
                    price: notebook.price,
                    place: notebook.place,

                }
            });
            return getNoteBooks;
        },
        getNumNoteBooks: state => {
            var getNumNoteBooks = state.notebooks.length;
            return getNumNoteBooks;
        },

        getEvents: state => {
            var getEvents = state.events.map(event => {
                return {
                  name: event.event_name,
                  adult: event.event_adults,
                  type: event.event_type,
                  category: event.event_category,
                  image: event.photo,
                }
            });
            return getEvents;
        },
        getTicketInCart: state => {
            var getTicketInCart = state.cartTickets.map(ticket => {
                return {
                    name: '$$' + ticket.name + '$$',
                    startdate: ticket.startdate,
                    starttime: ticket.starttime,
                    enddate: ticket.enddate,
                    endtime: ticket.endtime,
                    price: ticket.price,
                    image: ticket.image,
                    place: ticket.place
                }
            });
            console.log(getTicketInCart)
            return getTicketInCart;
        },
        getNumberInCart: state => {
            var getNumberInCart = state.cartTickets.length;
            return getNumberInCart;
        }
        
    },

    mutations: {
        ADD_TICKET: (state, ticket) => {
            state.cartTickets.push(ticket);
            // console.log(state.cartTickets);
            // console.log('mutation is performed');
            // console.log(state.cartTickets.length)
        },
        REMOVE_TICKET: (state, index) => {
          state.cartTickets.splice(index, 1);
        },
    },

    actions: {
        addTicket: (context, ticket) => {
            // console.log('ticket')
            context.commit('ADD_TICKET', ticket);
            console.log(this.state.cartTickets)
        },
        removeTicket: (context, index) => {
            context.commit('REMOVE_TICKET', index);
        }
    }
