<template>
<div>
    <Header v-bind:panel_header="false"></Header>
    <v-content id="ticketslist">
        <v-container>
            <v-row>
                <v-col md="3">
                </v-col>
                <v-spacer></v-spacer>
                <v-col md="3">
                    <!-- <v-btn block class="title mt-5 white--text" color="orange" height="50px" @click.stop="drawer = !drawer">Create Ticket</v-btn> -->
                </v-col>
            </v-row>
            <v-row justify="center">
                <v-col md="8">
                    <v-row>
                        <div v-for="(ticket, index) in tickets" :key="index">
                            <!-- <v-hover v-slot:default="{ hover }"> -->
                            <v-card class="ma-5" color="grey lighten-4" max-width="350px" shaped :disabled="card_disabled[index]">
                                <!-- <h1 style="color:black">{{ card_disabled[index] }}</h1> -->
                                <v-img :src="ticket.image" :aspect-ratio="16/9">
                                    <div v-if="card_disabled[index]">
                                        <v-btn class="title" color="orange" dark width="100px" height="150px">SOLD<br>OUT</v-btn>
                                    </div>
                                </v-img>
                                <v-card-text class="pt-6">
                                    <div class="headline black--text title mb-2">{{ ticket.name }}</div>
                                    <div class="font-weight-light grey--text title mb-2">{{ ticket.date }}</div>
                                    <div class="font-weight-light grey--text title mb-2">{{ ticket.price }}</div>
                                    <div class="font-weight-light grey--text title mb-2">{{ ticket.place }}</div>
                                    <v-btn class="white--text subtitle-1" color="orange" height="30" block @click="addTicketToCart(ticket, index)">ADD ONE MORE OR THIS</v-btn>
                                </v-card-text>
                            </v-card>
                            <!-- </v-hover> -->
                        </div>
                    </v-row>
                </v-col>
            </v-row>
        </v-container>
        <v-row justify="center" class="saleresume">
                    <v-container>
                        <v-simple-table fixed-header height="300px">
                            <template v-slot:default>
                            <thead>
                                <tr>
                                <th class="text-left">Name of event</th>
                                <th class="text-left">Type of Ticket</th>
                                <th class="text-left">Quantity</th>
                                <th class="text-left">Value</th>
                                <th class="text-left">Total</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in event_data" :key="index">
                                    <td>{{ item.name }}</td>
                                    <td>{{ item.type_ticket }}</td>
                                    <td>{{ item.quantity }}</td>
                                    <td>{{ item.value }}</td>
                                    <td>{{ item.total }}</td>
                                </tr>
                            </tbody>
                            </template>
                        </v-simple-table>
                        <v-row>
                            <v-spacer></v-spacer>
                            <v-col md="2">Total: ${{total_value}}</v-col>
                        </v-row>
                    </v-container>
                </v-row>
                <v-row justify="center">
                    <v-col md="8">
                    <v-btn class="mt-10 mb-10 white--text subtitle-1" block color="orange" height="40px">FINISH AND CHECKOUT WITH PAYPAL</v-btn>
                    </v-col>
                </v-row>
        </v-content>
    <Footer></Footer>
</div>

</template>

<script>
import Header from "./Header"
import Footer from "./Footer"
import 'babel-polyfill'
import myUpload from 'vue-image-crop-upload'
// import { mapActions, mapGetters } from 'vuex'
export default {
    // name: "TicketsList",
    components: {
        "Header": Header,
        'my-upload': myUpload,
        "Footer": Footer
    },
    data() {
        return {
            card_disabled: [],
            total_value: 0,
            event_data: [
                {
                    name: 'Special anicomix san valentine',
                    type_ticket: 'General',
                    quantity: 2,
                    value: 200,
                    total: 400
                },
                {
                    name: 'Special anicomix san valentine',
                    type_ticket: 'VIP',
                    quantity: 1,
                    value: 300,
                    total: 300
                }
            ]
        }
    },
    computed: {
        tickets() {
            return this.$store.getters.getNoteBooks;
        },
        num_tickets() {
            return this.$store.getters.getNumNoteBooks;
            // console.log(this.$store.getters.getNumNoteBooks)
        }
    },
    created: function () {
        var s=0;
        for (let index = 0; index < this.$store.getters.getNumNoteBooks; index++){
            this.card_disabled[index] = false;
        }
        for(let i=0; i<this.event_data.length; i++){
            this.total_value = this.total_value + this.event_data[i].total;
        }
        console.log(this.total_value)
         
            
    },

    methods: {
        addTicketToCart(ticket, index) {
            this.$store.commit('ADD_TICKET', ticket)
            // this.card_disabled[index] = true
            // var disable = []
            // for (let i = 0; i < this.$store.getters.getNumNoteBooks; i++)
            //     disable[i] = this.card_disabled[i]
            // this.card_disabled = disable
            // console.log(this.card_disabled)
        },

        cancelEditTicket() {
            this.$refs.form.reset();
            this.drawer = !(this.drawer);

        },
        toggleShow() {
            this.uploadshow = !this.uploadshow;
        },
        cropSuccess(imgDataUrl, field) {
            console.log('-------- crop success --------');
            this.imgDataUrl = imgDataUrl;
            this.uploaded = !(this.uploaded);
        },
        cropUploadSuccess(jsonData, field) {
            console.log('-------- upload success --------');
            console.log(jsonData);
            console.log('field: ' + field);
        },
        cropUploadFail(status, field) {
            console.log('-------- upload fail --------');
            console.log(status);
            console.log('field: ' + field);
        },
    },
}
</script>

<style lang="scss" scoped>
#ticketslist {
    // background-image: -webkit-linear-gradient( -29deg, rgb(193, 15, 65) 0%, rgb(35, 59, 136) 100%);
    background: white;
}

.v-card--reveal {
    align-items: center;
    bottom: 0;
    justify-content: center;
    opacity: 0.5;
    position: absolute;
    width: 100%;
}
.saleresume {
    margin-top: 50px;
}
</style>




