<template>
<div>
    <v-content id="eventslist">
        <v-row justify="center" id="eventsearch">
            <div>
                <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">MY EVENTS</h1>
            </div>
        </v-row>
        <v-container class="mt-10">
            <v-row justify="center">
                <v-col md="8">
                    <v-row justify="center">
                        <v-col md="3">
                        <v-text-field class="white--text ml-5" v-model="search_name" background-color="white" label="Search" single-line outlined></v-text-field>
                        </v-col>
                        <v-col md="3">
                            <v-select :items="event_month" label="ie, July" background-color="white" outlined single-line></v-select>
                                                    
                        </v-col>
                        <v-col md="3">
                            <v-select :items="event_year" label="ie, 2019" background-color="white" outlined single-line></v-select>
                                                    
                        </v-col>
                        <v-col md="3">
                            <v-btn dark color="orange" height="55px" width="150px"><span class="title">BUSCAR</span></v-btn>
                        </v-col>
                    </v-row>
                </v-col>
                
            </v-row>
        </v-container>
        
        <v-row justify="center">
            <v-col md="10" id="eventlist">
                <div style="text-align:center">
                    <div id="showevent">
                        <v-container>
                            <v-row justify="center">
                                <v-col md="10">
                                        <div v-for="event in events" class="mt-5">
                                            <v-hover v-slot:default="{ hover }">
                                                <v-expand-transition>
                                                <v-card class="ma-5" color="#952175">
                                                    <div class="d-flex flex-no-wrap justify-space-between">
                                                        <v-row>
                                                            <v-col md="5">
                                                                <v-avatar class="mt-5" size="200" tile>
                                                                    <v-img :src="event.image">
                                                                    </v-img>
                                                                </v-avatar>
                                                            </v-col>
                                                            <v-col md="4">
                                                                <div class="mt-7">
                                                                    <div class="headline white--text" v-text="event.name"></div>
                                                                    <div class="font-weight-light grey--text title" v-text="event.type"></div>
                                                                    <div>
                                                                        <div class="font-weight-light grey--text title mb-2">Type of only adults: {{ event.adults }}</div>
                                                                        <div class="font-weight-light grey--text title mb-2">Type of event: {{ event.type }}</div>
                                                                        <div class="font-weight-light grey--text title mb-2">Event Category: {{ event.category }}</div>
                                                                        <!-- <v-select :items="event_option" label="Event Options" color="white" solo background-color="transparent"></v-select> -->
                                                                    </div>
                                                                </div>
                                                            </v-col>
                                                            <v-col md="3" class="mt-7"> 
                                                                <v-btn class="white--text mt-7 mr-2" height="40px" width="150px" outlined>
                                                                    <font style="font-size:1.5rem">View</font>
                                                                </v-btn>
                                                                <v-menu>
                                                                <template v-slot:activator="{ on: menu }">
                                                                    <v-tooltip bottom>
                                                                    <template v-slot:activator="{ on: tooltip }">
                                                                        <v-btn class="white--text mt-10 mr-2" v-on="{ ...tooltip, ...menu }" outlined height="40px" width="150px"><font style="font-size:1.5rem">Options</font></v-btn>
                                                                    </template>
                                                                    <span>For editing options</span>
                                                                    </v-tooltip>
                                                                </template>
                                                                <v-list>
                                                                    <v-list-item
                                                                    v-for="(option, index) in event_option" :key="index" @click="{{ option.path }}">
                                                                    <v-list-item-title>{{ option.title }}</v-list-item-title>
                                                                    </v-list-item>
                                                                </v-list>
                                                                </v-menu>
                                                            </v-col>
                                                        </v-row>
                                                    </div>
                                                </v-card>
                                                </v-expand-transition>
                                            </v-hover>
                                        </div>
                                </v-col>
                            </v-row>
                        </v-container>
                    </div>
                </div>
            </v-col>
        </v-row>
    </v-content>
</div>
</template>
<script>
import Header from './Header'
export default {
    name: 'EventsList',
    components: {
        "Header": Header
    },
    data() {
        return {
            search_name: '',
            event_status: ['Done', 'Comming up', 'Cancelled'],
            profile_show: false,
            tickets_show: false,
            events_show: false,
            producer_show: false,
            resume_show: false,
            free_show: false,
            dialog: false,
            show: false,
            event_option: [
                {   title: 'Edit kind of tickets',
                    path: '#'
                },
                {   title: 'Edit Events',
                    path: '#' },
                {   title: 'Show sales event',
                    path: '#'
                },
            ],
            event_edit: [
                {title: 'Data of event'},
                {title: 'Kind of tickets'},
                {title: 'JRPs'}
            ],
            event_countries: [],
            event_provinces: [],
            event_adults: [],
            event_name: '',
            event_month: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sept', 'Oct', 'Nov','Dec'],
            event_year: ['2022','2021','2020', '2019', '2018', '2017', '2016', '2015'],
            nameRules: [
                v => !!v || 'Name is required',
                v => (v && v.length <= 10) || 'Name must be less than 10 characters',
            ],
            event_categories: [],
            event_artist: '',
            event_description: '',
            event_areas: [],
            event_address: '',
            event_place: '',
            event_video: '',


        }
    },
    computed: {
        events() {
            console.log(this.$store.getters.getEvents);
            return this.$store.getters.getEvents;
        }
    },
}
</script>
<style lang="scss" scoped>
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .5;
  position: absolute;
  width: 100%;
}
#eventslist{
    background:white;
    background-size: 100%, 100%;
}
#menu {
    background: #e8edea;
    background-size: 100%, 100%;
}
#eventlist {
    background: white;
    background-size: 100%, 100%;
}
#eventsearch {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}
</style>


