<template>
    <div>
        <v-content>
            <v-row justify="center" id="myticketheadline">
                <div>
                    <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">MY TICKETS(WHO I BOUGHT)</h1>
                </div>
            </v-row>
            <v-container>
                <v-row justify="center">
                <v-col md="8">
                    <v-row>
                    <div v-for="(ticket, index) in tickets" :key="index">
                    <!-- <v-hover v-slot:default="{ hover }"> -->
                        <div v-if=""></div>
                        <v-card class="ma-5" color="grey lighten-4" max-width="350px" :disabled="card_disabled[index]">
                            <v-img :src="ticket.image" :aspect-ratio="16/9">
                                <div v-if="card_disabled[index]">
                                    <v-btn class="title" color="blue" dark width="200px" height="150px">Older<br>CARD</v-btn>
                                </div>
                            </v-img>
                            <v-card-text class="pt-6" style="position: relative;">
                                <div class="headline black--text title mb-2">{{ ticket.name }}</div>
                                <div class="font-weight-light grey--text title mb-2">From {{ ticket.startdate + ", " + ticket.starttime}}</div>
                                <div class="font-weight-light grey--text title mb-2">To {{ ticket.enddate + ", " + ticket.endtime}}</div>
                                <div class="font-weight-light grey--text title mb-2">{{ ticket.price }}</div>
                                <div class="font-weight-light grey--text title mb-2">{{ ticket.place }}</div>
                                <div class="text-center">
                                    <v-dialog
                                    v-model="transfer_dialog"
                                    width="500"
                                    overlay-color="transparent"
                                    :dark="transfer_dark"
                                    >
                                    <template v-slot:activator="{ on }">
                                        <v-btn class="mb-3" dark color="orange" block v-on="on">TRANSFER TICKET</v-btn>
                                    </template>

                                    <v-card width="500px" height="300px">
                                        <div style="text-align:center">
                                            <br><br>
                                            <img src="../assets/img/kindpng_209992.png" width="60px" height="60px"></v-img>
                                        </div>
                                        <v-card-text>
                                            <h3 style="color:black">To transfer tickets you must download the app</h3>
                                        </v-card-text>
                                        <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="orange" dark block @click="transfer_dialog = false">DOWNLOAD NOW</v-btn>
                                        </v-card-actions>
                                    </v-card>
                                    </v-dialog>
                                </div>
                                <div class="text-center">
                                    <v-dialog
                                    v-model="qr_dialog"
                                    width="500"
                                    overlay-color="transparent"
                                    :dark="qr_dark"
                                    >
                                    <template v-slot:activator="{ on }">
                                        <v-btn class="mb-3" dark color="orange" block v-on="on">GENERATE QR</v-btn>
                                    </template>

                                    <v-card width="500px" height="300px">
                                        <div style="text-align:center">
                                            <br><br>
                                            <img src="../assets/img/email.png" width="60px" height="60px"></v-img>
                                        </div>
                                        <v-card-text>
                                            <div style="text-align:center">
                                                <h3 style="color:black">The QR code was sento to your email.<br>Download the app for easier use.</h3>
                                            </div>
                                        </v-card-text>
                                        <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="orange" dark block @click="qr_dialog = false">DOWNLOAD NOW</v-btn>
                                        </v-card-actions>
                                    </v-card>
                                    </v-dialog>
                                </div>
                            </v-card-text>
                        </v-card>
                    <!-- </v-hover> -->
                </div>
                </v-row>
                <v-btn class="mt-10 mb-10" block color="#c3c4c7" height="50px">
                    <font style="color:white;font-size:20px">SEE MORE</font>
                </v-btn>
                </v-col>
            </v-row>
            </v-container>
        </v-content>
    </div>
</template>
<script>
export default {
    computed: {
        tickets() {
            return this.$store.getters.getNoteBooks;
        },
        num_tickets() {
            return this.$store.getters.getNumNoteBooks;
            // console.log(this.$store.getters.getNumNoteBooks)
        }
    },
    created() {
        for (let index = 0; index < this.$store.getters.getNumNoteBooks; index++)
            this.card_disabled[index] = false
        // console.log(this.now_date)
        // console.log(this.card_disabled)
        // console.log(this.now_date.getFullYear())
        // console.log(this.now_date.getMonth())
        // console.log(typeof(this.now_date.getDate()))
        // console.log(this.now_date.getTime())
        // console.log(this.now_date.getHours())
        var check_ticket = this.$store.getters.getNoteBooks;
        for(let i=0;i<check_ticket.length;i++){
            var compare_date = check_ticket[i].enddate.split("-");
            var compare_time = check_ticket[i].endtime.split(":");
            // console.log(compare_date);
            // console.log(compare_time);
            // console.log(this.now_date.getMonth()+1)
            if(Number(compare_date[0]) < this.now_date.getFullYear()){
                this.card_disabled[i] = true;
                continue;
            }
            if(Number(compare_date[0] = this.now_date.getFullYear())) {
                if(Number(compare_date[1] < (this.now_date.getMonth()+1))) {
                    this.card_disabled[i] = true;
                    continue;
                }
                if(Number(compare_date[1] == (this.now_date.getMonth()+1))) {
                    if(Number(compare_date[2] < (this.now_date.getDate()))) {
                        this.card_disabled[i] = true;
                        continue;
                    }
                    if(Number(compare_date[2] == (this.now_date.getDate()))) {
                        if(Number(compare_time[0] < (this.now_date.getHours()))) {
                            this.card_disabled[i] = true;
                            continue;
                        }
                        if(Number(compare_time[0] == (this.now_date.getHours()))) {
                            if(Number(compare_time[1] < (this.now_date.getMinutes()))) {
                                this.card_disabled[i] = true;
                                continue;
                            }
                        }
                    }
                }
            }
        
        }
        console.log(this.card_disabled)
        // var date1 = moment(this.now_date).format("dd-mm-yyyy");
        // console.log(date1)
    },
    cardDisbledFunc() {
        
       
    },
    data() {
        return {
            valid: true,
            page: 1,
            show: false,
            
            transfer_dialog: false,
            qr_dialog: false,
            transfer_dark: false,
            qr_dark: false,
            // tickets: this.$store.state.notebooks,
            time: null,
            date: null,
            starttime: false,
            start_time: '',
            endtime: false,
            start_date: '',
            now_date: new Date(),
            start_date_menu: false,
            end_time: '',
            end_date: '',
            end_date_menu: false,
            card_disabled: [],

        }
    },
    methods: {

    },
}
</script>
<style lang="scss" scoped>
#myticketheadline {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}
</style>


