<template>
<v-content>
    <v-row justify="center" id="editeventheadline">
        <v-navigation-drawer v-model="drawer" absolute temporary floating right width="500px" height="800px">
            <!-- <v-row ></v-row> -->
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="headline font-weight-bold">Create Ticket</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-row justify="center">
                <v-col md="10">
                    <v-form ref="form" style="margin-top: 5%;" v-model="valid" lazy-validation>
                        <v-text-field v-model="editedItem.name" :rules="nameRules" label="Ticket Name" :counter="20" single-line outlined require></v-text-field>
                        <v-textarea v-model="ticket_description" solo label="Ticket Description"></v-textarea>
                        <v-text-field v-model="editedItem.price" type="number" prepend-inner-icon="mdi-currency-usd" label="Price" single-line outlined></v-text-field>
                        <v-row>
                            <v-col md="6">
                                <label>Starting Date and Time:</label>
                                <v-menu ref="start_date_menu" v-model="start_date_menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                <template v-slot:activator="{ on }">
                                <v-text-field v-model="start_date" label="Picker in menu" prepend-icon="event" readonly v-on="on"></v-text-field>
                                </template>
                                <v-date-picker v-model="start_date" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.start_date_menu.save(start_date)">OK</v-btn>
                                </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col md="6">
                                <div class="mt-7">
                                <v-menu ref="start_menu" v-model="starttime" :close-on-content-click="false" :nudge-right="40" :return-value.sync="time" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                <template v-slot:activator="{ on }">
                                <v-text-field v-model="start_time" label="Start time:" prepend-icon="access_time" v-on="on"></v-text-field>
                                </template>
                                <v-time-picker v-if="starttime" v-model="start_time" full-width @click:minute="$refs.start_menu.save(start_time)"></v-time-picker>
                                </v-menu>
                                </div>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col md="6">
                                <label>Ending Date and Time:</label>
                                <v-menu ref="end_date_menu" v-model="end_date_menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                <template v-slot:activator="{ on }">
                                <v-text-field v-model="end_date" label="Picker in menu" prepend-icon="event" readonly v-on="on"></v-text-field>
                                </template>
                                <v-date-picker v-model="end_date" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="$refs.end_date_menu.save(end_date)">OK</v-btn>
                                </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col md="6">
                                <div class="mt-7">
                                    <v-menu ref="end_menu" v-model="endtime" :close-on-content-click="false" :nudge-right="40" :return-value.sync="time" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                    <template v-slot:activator="{ on }">
                                    <v-text-field v-model="end_time" label="End time:" prepend-icon="access_time" v-on="on"></v-text-field>
                                    </template>
                                    <v-time-picker v-if="endtime" v-model="end_time" full-width @click:minute="$refs.end_menu.save(endtime)"></v-time-picker>                                   
                                    </v-menu>
                                </div>
                            </v-col>
                        </v-row>
                        <!-- <label>Choose Event Image:</label>
                        <div v-if="uploaded">
                            <img :src="imgDataUrl" width="200px" height="200px">
                        </div>
                        <div v-if="!uploaded">
                            <img src="../assets/img/producer1.png" width="200px" height="200px">
                        </div>
                        <v-btn class="mt-3 ml-5" @click="toggleShow">Choose Image</v-btn>
                        <my-upload field="img"
                        @crop-success='cropSuccess'
                        @crop-upload-success='cropUploadSuccess'
                        @crop-upload-fail='cropUploadFail'
                        v-model="uploadshow"
                        :width="200"
                        :height="200"
                        url=""
                        :params="params"
                        :headers="headerimg"
                        lang-type="en"
                        :value.sync="uploadshow"
                        img-format="png">
                        </my-upload> -->
                        <v-row>
                            <v-col md="6">
                                <div class="mt-10">
                                    <v-btn color="" block @click="close()">Cancel</v-btn>
                                </div>
                                
                            </v-col>
                            <v-col md="6">
                                <div class="mt-10">
                                    <v-btn color="" block @click="saveTicket()">Save</v-btn>
                                </div>
                                
                            </v-col>
                        </v-row>
                    </v-form>
                </v-col>
            </v-row>
        </v-navigation-drawer>
        <div>
            <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">EDIT EVENT</h1>
        </div>
    </v-row>
    <v-container>
        <v-row>
            <v-col md="3">
                <span class="ml-10">Event Id: 41654654</span>
            </v-col>
            <v-spacer></v-spacer>
            <v-col md="3">
                <span>Validation token: 6dfg687464grt</span>
            </v-col>
        </v-row>
        
        <v-card>
            <v-tabs v-model="tab" background-color="transparent" grow>
            <v-tab v-for="item in items" :key="item"><h2 class="grey--text">{{ item }}</h2></v-tab>
            </v-tabs>
            <v-tabs-items v-model="tab">
            <v-tab-item>
                <v-card flat>
                <v-card-text>
                    <EventCreateLogged></EventCreateLogged>
                </v-card-text>
                </v-card>
            </v-tab-item>
            <v-tab-item>
                <v-container>
                    <v-row justify="center" class="ticket_type">
                        <v-col md="10">
                            
                            <v-data-table :headers="headers" :items="ticket_type" class="elevation-1">
                                <template v-slot:top>
                                <v-toolbar flat color="#c3c4c7">
                                    <v-toolbar-title>TICKETS</v-toolbar-title>
                                    <v-divider class="mx-4" inset vertical></v-divider>
                                    <v-spacer></v-spacer>
                                </v-toolbar>
                                </template>
                                <template v-slot:item.status ="{ item }">
                                    <select>
                                        <option>Select</option>
                                        <option>Allowed</option>
                                        <option>Closed</option>
                                        <option>Suspend</option>
                                        <option>Removed</option>
                                    </select>
                                </template>
                            </v-data-table>
                            <v-row>
                            <v-spacer></v-spacer>
                            <v-col md="3">
                                <v-btn class="subtitle-1 mt-5 white--text" color="orange" height="50px" width="150px" @click.stop="drawer = !drawer">Create Ticket</v-btn>
                            </v-col>
                            </v-row>
                        </v-col>
                    </v-row>
                    
                    <!-- <v-row justify="center">
                        <v-col md="3">
                            <v-btn class="white--text title" color="orange" height="55px" width="150px">Add</v-btn>
                        </v-col>
                    </v-row> -->
                </v-container>
            </v-tab-item>
            <v-tab-item>
                <v-row justify="center" class="saleresume">
                    <v-container>
                        <v-simple-table fixed-header height="300px">
                            <template v-slot:default>
                            <thead>
                                <tr>
                                <th class="text-left">Name of ticket</th>
                                <th class="text-left">On sale</th>
                                <th class="text-left">Bought</th>
                                <th class="text-left">Gifted</th>
                                <th class="text-left">Validated</th>
                                <th class="text-left">Value</th>
                                <th class="text-left">Total</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="item in sale_data" :key="item.sale_name">
                                    <td>{{ item.sale_name }}</td>
                                    <td>{{ item.onsale }}</td>
                                    <td>{{ item.bought }}</td>
                                    <td>{{ item.gifted }}</td>
                                    <td>{{ item.validated }}</td>
                                    <td>${{ item.value }}</td>
                                    <td>${{ item.total }}</td>
                                </tr>
                            </tbody>
                            </template>
                        </v-simple-table>
                        <v-row>
                            <v-spacer></v-spacer>
                            <v-col md="2">
                                <div>Total: ${{ total_value }}</div>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-row>
            </v-tab-item>
            </v-tabs-items>
        </v-card>
        
        
    </v-container>
    
</v-content>
</template>
<script>
import EventCreateLogged from './EventCreateLogged'
import 'babel-polyfill'
import myUpload from 'vue-image-crop-upload' 
export default {
    components: {
        "EventCreateLogged": EventCreateLogged,
        'my-upload': myUpload
    },
    data() {
        return {
        tab: null,
        dialog: false,
        items: [
          'Edit Event', 'Ticket Types', 'Sales Result'
        ],
        headers: [
            {
            text: 'NAME',
            align: 'left',
            value: 'name',
            },
            { text: 'PRICE', value: 'price' },
            { text: 'START DATE', value: 'startdate' },
            { text: 'END DATE', value: 'enddate' },
            { text: 'STATUS', value: 'status', sortable: false },
            // { text: 'EDIT', value: 'action', sortable: false },
        ],
        ticket_type: [
          {
            name: 'General',
            price: '$200',
            startdate: "2020-2-17 20:30",
            enddate: '2020-2-25 20:30',
            ticket_status: ["Active", "Closed", "Suspended", "Removed"],
          },
          {
            name: 'Vip',
            price: '$300',
            startdate: "2020-2-17 20:30",
            enddate: '2020-2-25 20:30',
            ticket_status: ["Active", "Closed", "Suspended", "Removed"],
          },
          {
            name: 'General',
            price: '$200',
            startdate: "2020-2-17 20:30",
            enddate: '2020-2-25 20:30',
            ticket_status: ["Active", "Closed", "Suspended", "Removed"],
          },
          {
            name: 'Vip',
            price: '$300',
            startdate: "2020-2-17 20:30",
            enddate: '2020-2-25 20:30',
            ticket_status: ["Active", "Closed", "Suspended", "Removed"],
          },
          {
            name: 'General',
            price: '$200',
            startdate: "2020-2-17 20:30",
            enddate: '2020-2-25 20:30',
            ticket_status: ["Active", "Closed", "Suspended", "Removed"],
          },
          {
            name: 'Vip',
            price: '$300',
            startdate: "2020-2-17 20:30",
            enddate: '2020-2-25 20:30',
            ticket_status: ["Active", "Closed", "Suspended", "Removed"],
          },
        ],
        ticket_type_status: ['Blocked', 'Allowed', 'Suspended'],
        editedIndex: -1,
        editedItem: {
            name: '',
            price: '',
            startdate: '',
            enddate: '',
            status: '',
        },
        defaultItem: {
            name: '',
            price: '',
            startdate: '',
            enddate: '',
            status: '',
        },
        date: null,
        time: null,
        starttime: false,
        start_time: '',
        endtime: false,
        end_time: '',
        start_date: '',
        start_date_menu:false,
        end_date: '',
        end_date_menu:false,
        valid: true,
        ticket_name: '',
        ticket_description: '',
        ticket_price: '',
        ticket_quantity: '',
        ticket_startdate: '',
        ticket_starttime: '',
        ticket_enddate: '',
        ticket_endtime: '',
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length <= 20) || 'Name must be less than 20 characters',
        ],
        uploadshow: false,
        params: {
            token: '123456798',
            name: 'avatar'
        },
        headerimg: {
            smail: '*_~'
        },
        imgDataUrl: '',
        on: false,
        page: 1,
        show: false,
        dialog: false,
        uploaded: false,
        // tickets: this.$store.state.notebooks,
        drawer: null,
        // saleresume table data
        total_value: 0,
        sale_data: [
          {
            sale_name: 'General',
            onsale: 97,
            bought: 2,
            gifted: 1,
            validated: 0,
            value: 200,
            total: 400,
          },
          {
            sale_name: 'Vip',
            onsale: 29,
            bought: 1,
            gifted: 0,
            validated: 0,
            value: 300,
            total: 300,
          },
          {
            sale_name: 'General',
            onsale: 97,
            bought: 2,
            gifted: 1,
            validated: 0,
            value: 200,
            total: 400,
          },
          {
            sale_name: 'Vip',
            onsale: 29,
            bought: 1,
            gifted: 0,
            validated: 0,
            value: 300,
            total: 300,
          },
          {
            sale_name: 'General',
            onsale: 97,
            bought: 2,
            gifted: 1,
            validated: 0,
            value: 200,
            total: 400,
          },
          {
            sale_name: 'Vip',
            onsale: 29,
            bought: 1,
            gifted: 0,
            validated: 0,
            value: 300,
            total: 300,
          },
          {
            sale_name: 'General',
            onsale: 97,
            bought: 2,
            gifted: 1,
            validated: 0,
            value: 200,
            total: 400,
          },
          {
            sale_name: 'Vip',
            onsale: 29,
            bought: 1,
            gifted: 0,
            validated: 0,
            value: 300,
            total: 300,
          },
          
        ],
        }
        
    },
    created() {
        this.initialize()
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    methods: {
        initialize() {
            for (let i=0; i<this.sale_data.length;i++){
                this.total_value = this.total_value + this.sale_data[i].total;
            }
            console.log(this.total_value)
        },
      close () {
        this.drawer = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      saveTicket () {
        this.editedItem.startdate = this.start_date.concat(' ', this.start_time);
        this.editedItem.enddate = this.end_date.concat(' ', this.end_time);
        console.log(this.editedItem)
        console.log(this.editedIndex)
        // if (this.editedIndex > -1) {
        //   Object.assign(this.tikcet_type[this.editedIndex], this.editedItem)
        // } else {
        this.ticket_type.push(this.editedItem)
        //   console.log(this.ticket_type)
        // }
        var str = [];
        for (let i=0; i<this.ticket_type.length;i++)
            str[i] = this.ticket_type[i];
        this.ticket_type = str;
        // console.log(str)
        this.close()
      },
      toggleShow() {
				this.uploadshow = !this.uploadshow;
      },
      cropSuccess(imgDataUrl, field){
                console.log('-------- crop success --------');
                this.imgDataUrl = imgDataUrl;
                this.uploaded = !(this.uploaded);
      },
      cropUploadSuccess(jsonData, field){
                console.log('-------- upload success --------');
                console.log(jsonData);
                console.log('field: ' + field);
      },
      cropUploadFail(status, field){
                console.log('-------- upload fail --------');
                console.log(status);
                console.log('field: ' + field);
      },
    }
}
</script>
<style lang="scss" scoped>
#editeventheadline {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}

</style>


