<template>
    <div id="controlpanelform">
        <v-content>
            <v-row justify="center" id="controlpanelheadline">
                <div>
                    <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">MY CONTROL PANEL</h1>
                </div>
            </v-row>
            <v-container>
                <v-row class="mt-10">
                    <v-spacer></v-spacer>
                    <v-col md="10">
                        <v-row>
                            <svg width="20px" height="50px">
                                <rect width="10" height="50" style="fill:rgb(255, 153, 0)"></rect>
                            </svg>
                            <h2 class="black--text font-weight-bold">Usuario</h2>
                        </v-row>
                        
                    </v-col>
                </v-row>
                <v-row justify="center">
                    <v-col md="8">
                        <v-row justify="center">
                            <v-col md="4">
                                <div>
                                    <button class="button mb-8" width="200px" height="150px">
                                    <div><img src="../assets/img/panel/kindpng_209992.png" width="60px" height="60px"></div>
                                    <div class="black--text subtitle-1">DOWNLOAD</div>
                                    </button>
                                </div>
                                <div>
                                    <v-row>
                                        <svg width="20px" height="50px">
                                        <rect width="10" height="50" style="fill:rgb(255, 153, 0)"></rect>
                                    </svg>
                                    <h2 class="black--text font-weight-bold mb-10">Productora</h2>
                                    </v-row>
                                    <button class="button" width="200px" height="150px">
                                    <div><img src="../assets/img/panel/event.png" width="120px" height="120px"></div>
                                    <div class="black--text subtitle-1">MY EVENTS</div>
                                    </button>
                                </div>
                                <div>
                                    <button class="button mt-5" width="300px" height="200px">
                                    <div><img src="../assets/img/panel/usd.jpg" width="120px" height="120px"></div>
                                    <div class="black--text subtitle-1">FINANCIAL RESUME</div>
                                    </button>
                                </div>
                            </v-col>
                            <v-col md="4">
                                <div>
                                    <button class="button mb-10" width="200px" height="150px">
                                    <div><img src="../assets/img/panel/editmyprofile.png" width="120px" height="120px"></div>
                                    <div class="black--text subtitle-1">EDIT MY PROFILE</div>
                                </button>
                                </div>
                                <div>
                                    <button class="button" width="300px" height="200px" style="margin-top:80px">
                                    <div><img src="../assets/img/panel/img_141364.png" width="80px" height="120px"></div>
                                    <div class="black--text subtitle-1">MY PROFILE</div>
                                </button>
                                </div>
                                <div>
                                    <button class="button mt-5" width="300px" height="200px">
                                    <div><img src="../assets/img/panel/Untitled-1-06-512.png" width="80px" height="120px"></div>
                                    <div class="black--text subtitle-1">SEND FREE TICKETS</div>
                                </button>
                                </div>
                            </v-col>
                            <v-col md="4">
                                <div>
                                    <button class="button mb-10">
                                    <div><img src="../assets/img/panel/ticket.png" width="80px" height="120px"></div>
                                    <div class="black--text subtitle-1">ALL MY TICKETS</div>
                                    </button>
                                </div>
                                <div>
                                    <button class="button" style="margin-top:80px">
                                    <div><img src="../assets/img/panel/myjrp.png" width="100px" height="120px"></div>
                                    <div class="black--text subtitle-1">MY JRPS</div>
                                    </button>
                                </div>
                                
                            </v-col>
                        </v-row>
                    </v-col>
                </v-row>
            </v-container>
        </v-content>
    </div>
</template>
<script>
export default {
    name:"ControlPanelForm"
}
</script>
<style lang="scss" scoped>
#controlpanelheadline {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}
.button {
  position: relative;
  background-color: white;
  border: solid;
  border-color: orange;
  border-radius: 50px;
  font-size: 28px;
  color: #FFFFFF;
  padding: 20px;
  width: 400px;
  text-align: center;
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  height: 200px;
  width: 300px;
}

.button:after {
  content: "";
  background: #f1f1f1;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px !important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s;
  border: solid;
  border-color: orange;
//   border-radius: 50%;
}

.button:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s;
  border: solid;
  border-color: orange;
//   border-radius: 50px;
}
img {
    width: 100px;
    height: 120px;
}
</style>


