<template>
    <V-content id="login" style="fluid">
        <v-form ref="form" style="margin-top: 5%;" v-model="valid" lazy-validation>
            <v-container>
                <v-row justify="center" class="mt-7">
                    <h1>RADICALTICKETS</h1>
                </v-row>
                <div style="text-align:center; margin-top:20px">
                    <h2>Ingreso de usuarios</h2>
                </div>
                <v-row justify="center">
                    <v-col md="6">
                        <div id="logform">
                            <v-row justify="center" class="mt-10">
                                <v-col md="8">
                                <v-text-field 
                                v-model="email"
                                prepend-inner-icon="mdi-email" 
                                background-color="white" 
                                :rules="emailRules" 
                                label="Tu Email"
                                single-line 
                                outlined 
                                required
                                ></v-text-field>
                                <v-text-field
                                v-model="password"
                                prepend-inner-icon="mdi-key-variant"
                                background-color="white"
                                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" 
                                :rules="[passwordRules.required, passwordRules.min]"
                                :type="show ? 'text' : 'password'"                                   
                                label="Contrasea"
                                hint="At least 8 characters"
                                @click:append="show = !show"
                                outlined
                                single-line
                                ></v-text-field>
                                </v-col>
                            </v-row>
                            <v-row justify="center">
                                <v-col md="8">
                                    <v-checkbox v-model="checkbox" color="white">
                                        <template v-slot:label>
                                            <div style="color:white">Recordar</div>
                                        </template>
                                    </v-checkbox>
                                    <v-btn class="white--text" block height="50px" outlined>
                                        <font style="font-size:1.5rem">INGRESAR</font>
                                    </v-btn>
                                </v-col>
                            </v-row>
                            <br>
                            <v-row justify="center">
                                <router-link to="/user/forgetpass"><font style="font-size:1rem+; color:white">Perdiste tu contrasea?</font></router-link>
                            </v-row><br>
                        </div>
                    </v-col>
                </v-row>
            </v-container>
        </v-form>
    </v-content>
</template>
<script>
export default {
  name: "Login",
  data() {
    return {
      valid: true,
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      password: "Password",
      show: false,
      passwordRules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 8 || "Min 8 characters",
        // emailMatch: () => "The email and password you entered don't match"
      },
      recordar: false,
      checkbox: false
    };
  }
};
</script>
<style lang="scss" scoped>
#login {
    // background-image: url("../assets/img/realistic-leaves-with-neon-frame-background_52683-32328.jpg");
    background-image: -webkit-linear-gradient( -29deg, rgb(193, 15, 65) 0%, rgb(35, 59, 136) 100%);
    background-size: 100%, 100%;
}
.row .col-md-8 .v-input .v-input__control .v-input-has-state .v-input__control .v-input__slot .v-text-field__slot .theme--light .v-label{
    color: white;
}
</style>


