<template>
    <div>
        <Header v-bind:panel_header="false"></Header>
        <v-content>
            <v-container>
                <v-row justify="center" id="sendinvitation">
                    <div>
                        <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">SEND BULK INVITATIONS</h1>
                    </div>
                </v-row>
                <v-row justify="center">
                    <v-col md="7" class="mt-10">
                        <span class="display-1" >1.Download the</span>
                        <span class="display-1 orange--text"><a href="" download> excel file</a></span><br><br>
                        <span class="display-1">2.Add all the mails of people to be invited</span><br><br>
                        <span class="display-1">3.Reupload Here</span>
                    </v-col>
                </v-row>
                <v-row justify="center">
                    <!-- <v-col md="6"> -->
                        <div style="float">
                            
                            <input type="text" class="fileinput" style="width:300px" id="spnFilePath">
                            <v-btn id="btnFileUpload" class="white--text" color="orange" height="51px" rounded @click="fileUpload()">Open file</v-btn>
                            <input type="file" class="realfileinput" id="FileUpload1" >
                        </div>
                        
                            
                        
                    <!-- </v-col> -->
                </v-row>
                <v-row justify="center">
                    <v-col md="4">
                        <v-btn block color="orange"><span class="white--text subtitle-1">SEND NOW</span></v-btn>
                    </v-col>
                </v-row>
            </v-container>
        </v-content>
        <Footer></Footer>
    </div>
</template>
<script>
import Header from './Header'
import Footer from './Footer'
export default {
    components: {
        'Header': Header,
        'Footer': Footer,
    },
    methods: {
        fileUpload() {
            var fileupload = document.getElementById("FileUpload1");
            var filePath = document.getElementById("spnFilePath");
            var button = document.getElementById("btnFileUpload");
            fileupload.click();
            fileupload.onchange = function () {
            var fileName = fileupload.value.split('\\')[fileupload.value.split('\\').length - 1];
            console.log(fileName)
            // filePath.innerHTML = "<b>Selected File: </b>" + fileName;
            filePath.value = fileName;
            };
        }
    }
}
</script>
<style lang="scss" scoped>
#sendinvitation {
    background-color: #e8edea;
    background-size: 100%, 100%;
}
.fileinput {
    border: 2px solid orange;
    border-radius: 28px;
    height: 50px;
    margin-right: -90px;
    margin-top: 30px;
    margin-bottom:30px
}
.realfileinput {
    display: none;
}
a {
    text-decoration: none;
    color: #dd42f5
}

</style>


