<template>
<div id="myheader">
<v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
      right
      color="#333"
    >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="white--text">Home</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense>

        <v-list-item
          v-for="item in items"
          :key="item.title"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="white--text" @click="$router.push(item.path)">{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
<v-app-bar id="headerpage" height="70px">
    <!-- <v-app-bar-nav-icon class="ml-10"> -->
        
    <!-- </v-app-bar-nav-icon> -->
    <v-toolbar-title>
        <img src="../assets/img/radicalticketslogo.png" width="200px" height="40px" class="logo">
    </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn text class="white--text header_btn" large hidden-md-only @click="$router.push('/')"><h4>Start</h4></v-btn>
        <v-btn text class="white--text header_btn" large><h4>SERVICES</h4></v-btn>
        <v-btn text class="white--text header_btn" large><h4>SEARCH</h4></v-btn>
        <v-btn text class="white--text header_btn" large><h4>SIGN UP</h4></v-btn>
        <v-btn text class="white--text header_btn" large @click="$router.push('/ticket/myticket')"><h4>MY TICKETS</h4></v-btn>
        <v-btn text class="white--text header_btn" large><h4>LOG OUT</h4></v-btn>
        <v-btn text class="white--text header_btn" large @click="$router.push('/event/createlogged')"><h4>CREATE EVENT</h4></v-btn>
        <v-btn class="white--text" rounded color="orange" large @click="$router.push('/ticket/check')">
        <v-avatar class="ml-3" size="36">
            <span class="white--text headline">{{cartnumber}}</span>
        </v-avatar>
        <v-icon class="ml-3" large color="white">mdi-cart-outline</v-icon>
        </v-btn>
        <v-btn class="mx-2" id="drawer_btn" fab dark color="transparent" @click="drawer=!drawer">
        <v-icon dark>mdi-format-list-bulleted-square</v-icon>
        </v-btn>
</v-app-bar>


</div>
</template>
<script>
export default {
    name: "Header",
    props: {
        panel_header: false
    },
    computed: {
        cartnumber() {
            return this.$store.getters.getNumberInCart;
        }
    },
    data() {
        return {
            items: [
                { 
                    title: 'Start',
                    path: '/'
                },
                { 
                    title: 'Services',
                    path: ''
                },
                { 
                    title: 'Search',
                    path: ''
                },
                { 
                    title: 'Sign Up',
                    path: ''
                },
                { 
                    title: 'My tickets',
                    path: ''
                },
                { 
                    title: 'Log out',
                    path: ''
                },
                { 
                    title: 'Create event',
                    path: ''
                },
                
            ],
            lightsOut: false,
            drawer: false,
            window: false,
            dialog: false,
            tickets: this.$store.state.cartTickets,
        }
    }
}
</script>
<style lang="scss">
#headerpage {
    // background-image: url("../assets/img/simple-background-violet-gradient-wallpaper-preview.jpg");
    // background-image: url("../assets/img/hero-1-img.png");
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url("../assets/img/hero-1-img.png");
    // box-shadow: 10px 10px;
    // background: transparent;
    background-size: 100%, 100%;
    position: relative;
}

#panelheader {
    background-color:#f7f5f5;
    background-size: 100%, 100%;
}
#drawer_btn {
    display: none
}
@media screen and (max-width: 1400px) {
    .header_btn {
        display: none;
    }
    #drawer_btn {
        display: initial;
    }
}
</style>


