<template>
    <div>
        <Header v-bind:panel_header="false"></Header>
        <ProducerForm></ProducerForm>
        <Footer></Footer>
    </div>
</template>
<script>
import Header from './Header'
import ProducerForm from './ProducerForm'
import Footer from './Footer'
export default {
    components: {
        'Header': Header,
        'ProducerForm': ProducerForm,
        'Footer': Footer
    },
}
</script>


