<template>
    <v-content id="forgetpassword">
        <v-container>
            <v-row justify="center" style="margin-top: 10%;">
                <v-col md="6">
                    <h1 style="text-align:center">Recover Password</h1><br>
                    <h2>Enter your email and we will send you a link to change your password.</h2>
                </v-col>
            </v-row>
            <v-row justify="center">
                <v-col md="6">
                    <v-form ref="form" v-model="valid"  lazy-validation>
                        <v-text-field 
                        prepend-inner-icon="mdi-email" 
                        v-model="email" 
                        :rules="emailRules" 
                        label="Email" 
                        background-color="white"
                        single-line
                        outlined
                        required
                        ></v-text-field>
                        <!-- <router-link to="/user/resetpass">
                            <v-btn class="white--text" color="#bf095d" block height="50px" @click="fotgetPassword">
                                <font style="font-size:1.5rem">Forget Password</font>
                            </v-btn>
                        </router-link> -->
                        <v-btn class="white--text" color="#bf095d" block height="50px" @click="$router.push('/user/resetpass')">
                                <font style="font-size:1.5rem">Forget Password</font>
                        </v-btn>
                    </v-form>
                </v-col>
            </v-row>
        </v-container>
    </v-content>
</template>
<script>
export default {
    name: 'ForgetPassword',
    data() {
        return {
            email: '',
            emailRules: [
                v => !!v || "E-mail is required",
                v => /.+@.+\..+/.test(v) || "E-mail must be valid"
            ],
        }
    }
}
</script>
<style lang="scss" scoped>
#forgetpassword {
    background-image: -webkit-linear-gradient( -29deg, rgb(193, 15, 65) 0%, rgb(35, 59, 136) 100%);
    background-size: 100%, 100%;
}
</style>


