<template>
<div>
    <Header></Header>
    <v-content id="producerEdit">
        <v-row justify="center" id="eventlistheadline">
        <div>
            <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">EDIT PRODUCER PROFILE</h1>
        </div>
        </v-row>
        <v-container>
            <v-row justify="center" class="profileform">
                <v-col md="6">
                    <v-text-field 
                    class="white--text"
                    v-model="producer_name" 
                    background-color="white" 
                    :counter="10"
                    :rules="nameRules" 
                    label="Producer Name"
                    single-line
                    outlined 
                    required
                    ></v-text-field>
                
                    <v-row>
                        <v-col md="6">
                            <v-text-field 
                            class="white--text"
                            v-model="cuid" 
                            background-color="white" 
                            :counter="10"
                            :rules="cuidRules" 
                            label="CUID"
                            single-line
                            outlined 
                            required
                            ></v-text-field>
                        </v-col>
                        <v-col md="6">
                            <v-select 
                            :items="bill_types"  
                            label="Choose a bill type" 
                            background-color="white" 
                            outlined 
                            single-line
                            ></v-select>
                        </v-col>
                    </v-row>
                    <v-text-field 
                    class="white--text"
                    v-model="bank_account" 
                    background-color="white" 
                    :counter="10"
                    :rules="nameRules" 
                    label="Bank Account"
                    single-line
                    outlined 
                    required
                    ></v-text-field>
                    <v-textarea v-model="biography" outlined label="My Biography" background-color="white"></v-textarea>
                    <!-- <v-data-table
                        :headers="headers"
                        :items="headerfiles"
                        sort-by="calories"
                        class="elevation-1"
                    >
                        <template v-slot:top>
                        <v-toolbar flat color="white">
                            <v-toolbar-title>Header Files</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                        </v-toolbar>
                        </template>
                        <template v-slot:item.action="{ item }">
                        <v-icon small @click="deleteItem(item)">delete</v-icon>
                        </template>
                    </v-data-table> -->
                    <v-data-table :headers="headers" :items="headerfiles" class="elevation-1">
                        <template v-slot:top>
                        <v-toolbar flat color="#c3c4c7">
                            <v-toolbar-title>My Headerfiles</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                            <v-dialog v-model="dialog" max-width="500px">
                                <template v-slot:activator="{ on }">
                                    <v-btn class="white--text" color="orange" height="55px" width="150px" v-on="on">Add</v-btn>
                                </template>
                                <v-card>
                                    <v-card-title>
                                    <span class="headline">Add the headerfile</span>
                                    </v-card-title>

                                    <v-card-text>
                                    <v-container>
                                        <v-row justify="center">
                                            <v-col md="8">
                                                <v-text-field class="white--text" id="spnFilePath" v-model="cuid" background-color="white" :counter="10" single-line outlined></v-text-field>
                                                <input type="file" class="realfileinput" id="FileUpload1" >
                                            </v-col>
                                            <v-col md="3">
                                              <v-btn id="btnFileUpload" class="white--text" color="orange" height="50px" @click="fileUpload()">Open file</v-btn>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                    </v-card-text>

                                    <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="close()">Cancel</v-btn>
                                    <v-btn color="blue darken-1" text @click="save()">Save</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </v-toolbar>
                        </template>
                        <template v-slot:item.action="{ item }">
                            <!-- <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon> -->
                            <v-icon small @click="deleteItem(item)">delete</v-icon>
                        </template>
                        <!-- <template v-slot:no-data>
                        <v-btn color="primary" @click="initialize">Reset</v-btn>
                        </template> -->
                        <template v-slot:item.status ="{ item }">
                            <select>
                                <option>Select</option>
                                <option>Allowed</option>
                                <option>Closed</option>
                                <option>Suspend</option>
                                <option>Removed</option>
                            </select>
                        </template>
                    </v-data-table>
                    <div><v-btn class="white--text mt-10" color="orange">Save Profile</v-btn></div>
                </v-col>
            </v-row>
        </v-container>
    </v-content> 
</div>    
</template>
<script>
import Header from "./Header"
export default {
  name: 'ProducerForm',
  component: {
      "Header": Header
  },
  data() {
      return {
        valid: true,
        producer_name: '',
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length <= 10) || 'Name must be less than 10 characters',
        ],
        cuid: '',
        cuidRules: [
            v => !!v || 'CUID is required',
        ],
        bill_types: [],
        bank_account: '',
        biography: '',

        //datatable data
        editedIndex: -1,
        editedItem: {
                name: '',
        },
        dialog: false,
        headers: [
            {
            text: 'Filename',
            align: 'left',
            sortable: false,
            value: 'name',
            },
            
            { text: 'Actions', value: 'action', sortable: false },
        ],
        headerfiles: [
          {
            name: 'Header1.png',
          },
          {
            name: 'Header2.png',
          },
        ],
      }
  },
  computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },
  methods: {
      deleteItem (item) {
        const index = this.headerfiles.indexOf(item)
        // console.log(item)
        // console.log(this.headerfiles)
        confirm('Are you sure you want to delete this item?') && this.headerfiles.splice(index, 1)
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        this.editedItem.name = document.getElementById("spnFilePath").value
        console.log(this.editedItem)
        if (this.editedIndex > -1) {
          Object.assign(this.jrps[this.editedIndex], this.editedItem)
        } else {
          this.headerfiles.push(this.editedItem)
        }
        console.log(this.headerfiles)
        this.close()
      },
      fileUpload() {
        var fileupload = document.getElementById("FileUpload1");
        var filePath = document.getElementById("spnFilePath");
        var button = document.getElementById("btnFileUpload");
        fileupload.click();
        fileupload.onchange = function () {
        var fileName = fileupload.value.split('\\')[fileupload.value.split('\\').length - 1];
        console.log(fileName)
        // filePath.innerHTML = "<b>Selected File: </b>" + fileName;
        filePath.value = fileName;
        // this.file_name = fileName;
        };
      }
   
  }  
}
</script>
<style scoped>
#eventlistheadline {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}
.profileform {
    background-color: #e8edea;
    background-size: 100%, 100%;
}
.realfileinput {
    display: none;
}
</style>


