<template>
    <v-content>
        <v-row justify="center" id="myjrpform">
            <div>
                <h1 style="margin-top:30px; margin-bottom:30px;color:#c3c4c7">MY JRPS</h1>
            </div>
        </v-row>
        <v-container>
          <v-row>
            <!-- <v-spacer></v-spacer> -->
            <v-col md="10">
                <v-row justify="center">
                    <!-- <v-spacer></v-spacer> -->
                    <v-col md="7">
                        <v-text-field 
                        class="white--text ml-5"
                        v-model="name_dni"
                        background-color="white" 
                        label="Name or DNI"
                        single-line
                        outlined
                        ></v-text-field>
                    </v-col>
                    <v-col md="1">
                        <v-btn dark color="orange" height="55px" width="150px">Search</v-btn>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
        </v-container>
        <v-container>
            <v-row justify="center">
                <v-col md="10">
                    <v-data-table :headers="headers" :items="jrps" sort-by="dni" class="elevation-1">
                        <template v-slot:top>
                        <v-toolbar flat color="#c3c4c7">
                            <v-toolbar-title>My JRPS</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                            <v-dialog v-model="dialog" max-width="500px">
                                <template v-slot:activator="{ on }">
                                    <v-btn class="white--text" color="orange" height="55px" width="150px" v-on="on">Add</v-btn>
                                </template>
                                <v-card>
                                    <v-card-title>
                                    <span class="headline">{{ formTitle }}</span>
                                    </v-card-title>

                                    <v-card-text>
                                    <v-container>
                                        <v-row justify="center">
                                            <v-col cols="12" sm="8" md="6">
                                                <v-text-field v-model="editedItem.dni" label="DNI"></v-text-field>
                                            </v-col>
                                            <!-- <v-col cols="12" sm="6" md="4">
                                                <v-text-field v-model="editedItem.name" label="Name"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="4">
                                                <v-text-field v-model="editedItem.tel" label="Tel"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="4">
                                                <v-text-field v-model="editedItem.email" label="Email"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="4">
                                                <v-select :items="jrp_status" v-model="editedItem.status" label="ie, Allowed" background-color="white"></v-select>
                                            </v-col> -->
                                        
                                        </v-row>
                                    </v-container>
                                    </v-card-text>

                                    <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                                    <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </v-toolbar>
                        </template>
                        <!-- <template v-slot:item.action="{ item }">
                            <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
                            <v-icon small @click="deleteItem(item)">delete</v-icon>
                        </template> -->
                        <!-- <template v-slot:no-data>
                        <v-btn color="primary" @click="initialize">Reset</v-btn>
                        </template> -->
                        <template v-slot:item.status ="{ item }">
                            <select>
                                <option>Select</option>
                                <option>Allowed</option>
                                <option>Closed</option>
                                <option>Suspend</option>
                                <option>Removed</option>
                            </select>
                        </template>
                    </v-data-table>
                </v-col>
            </v-row>
            
            <!-- <v-row justify="center">
                <v-col md="3">
                    <v-btn class="white--text title" color="orange" height="55px" width="150px">Add</v-btn>
                </v-col>
            </v-row> -->
        </v-container>
    </v-content>
</template>
<script>
export default {
    name: 'MyJrpForm',    
    data() {
        return {
            dialog: false,
            name_dni: '',
            headers: [
                {
                text: 'DNI OR PASSPORT',
                align: 'left',
                sortable: false,
                value: 'dni',
                },
                { text: 'NAME', value: 'name' },
                { text: 'TEL', value: 'tel' },
                { text: 'EMAIL', value: 'email' },
                { text: 'STATUS', value: 'status', sortable: false },
                // { text: 'EDIT', value: 'action', sortable: false },
            ],
            jrps: [],
            editedIndex: -1,
            editedItem: {
                dni: '',
                name: '',
                tel: '',
                email: '',
                status: '',
            },
            defaultItem: {
                dni: '',
                name: '',
                tel: '',
                email: '',
                status: '',
            },

        }
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New JRP' : 'Edit JRP'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.jrps = [
          {
            dni: '123456789',
            name: 'Alegjandro',
            tel: 351264789,
            email: 'demo@demo.com',
            status: ''
          },
          {
            dni: 'AF123456',
            name: 'Vannia',
            tel: 351756482,
            email: 'vania@demo.com',
            status: 'Suspended'
          },
          {
            dni: '123456789',
            name: 'Alegjandro',
            tel: 351264789,
            email: 'demo@demo.com',
            status: 'Allowed'
          },
          {
            dni: 'AF123456',
            name: 'Vannia',
            tel: 351756482,
            email: 'vania@demo.com',
            status: 'Blocked'
          },
          {
            dni: '123456789',
            name: 'Alegjandro',
            tel: 351264789,
            email: 'demo@demo.com',
            status: 'Allowed'
          },
          {
            dni: 'AF123456',
            name: 'Vannia',
            tel: 351756482,
            email: 'vania@demo.com',
            status: 'Suspended'
          },
        ]
      },
      // editItem (item) {
      //   this.editedIndex = this.jrps.indexOf(item)
      //   this.editedItem = Object.assign({}, item)
      //   this.dialog = true
      // },

      // deleteItem (item) {
      //   const index = this.jrps.indexOf(item)
      //   confirm('Are you sure you want to delete this item?') && this.jrps.splice(index, 1)
      // },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.jrps[this.editedIndex], this.editedItem)
        } else {
          this.jrps.push(this.editedItem)
        }
        this.close()
      },
    },
}
</script>
<style lang="scss" scoped>
#myjrpform {
    background-color: #f7f5f5;
    background-size: 100%, 100%;
}
</style>


