                        <th class="text-left">Validated</th> 
                        <th class="text-left">Total</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in desserts" :key="item.name">
                        <td>{{ item.type }}</td>
                        <td>{{ item.bought }}</td>
                        <td>{{ item.validated }}</td>
                        <td>{{ item.total }}</td>
                        </tr>
                    </tbody>
                    </template>
                </v-simple-table>
