                total: 159,
            },
            {
                type: 'Frozen Yogurt',
                bought: 159,
                validated: 'Frozen Yogurt',
                total: 159,
            },
            {
                type: 'Frozen Yogurt',
                bought: 159,
                validated: 'Frozen Yogurt',
                total: 159,
            },
            
            ],
        }
        
    }
}
</script>
<style lang="scss" scoped>
#saleresume {
    background: #373737;
}
</style>


