<template>
<div id="createevent">
    <Header v-bind:panel_header="false"></Header>
    <v-content>
        <v-row justify="center" class="titlebg mt-10">
            <v-col md="6">
                <h1 style="text-align:center; margin-bottom:5%; color:black">New Event</h1>
            </v-col>
        </v-row>
        <v-row>
            <v-col md="2" id="tellus">
                <v-row justify="center">
                    <v-col md="10" class="mt-10">
                        <div style="margin-top:60px">
                            <h2 style="color:black">Tell us what<br> you love</h2>
                            <h4 style="color:black">Tap the event tags you would like to<br> see more often than the others</h4>
                        </div>
                    </v-col>
                </v-row>
            </v-col>
            <v-col md="10" class="mt-10">
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-row>
                        <v-col md="2"></v-col>
                        <v-col md="8">
                            <v-row justify="center">
                                <v-spacer></v-spacer>
                                <v-col md="12">
                                    <h2 style="text-align:center; margin-bottom:1%; color:black">Create User Account</h2>
                                    <label>Name:</label>
                                    <v-text-field v-model="user_name" label="Name" background-color="white" :rules="nameRules" outlined single-line require></v-text-field>
                                    <label>Surname:</label>
                                    <v-text-field v-model="user_surname" label="Surname" background-color="white" :rules="surnameRules" outlined single-line require></v-text-field>
                                    <label>Email</label>
                                    <v-text-field class="white--text" v-model="email" prepend-inner-icon="mdi-email" background-color="white" :rules="emailRules" label="Tu Email" single-line outlined required></v-text-field>
                                    <label>ID</label>
                                    <v-text-field 
                                    prepend-inner-icon="mdi-key-variant"
                                    v-model="id"
                                    background-color="white"
                                    :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" 
                                    :rules="[passwordRules.required, passwordRules.min]"
                                    :type="show ? 'text' : 'password'"
                                    label="id"
                                    hint="At least 8 characters"
                                    value=""
                                    class="input-group--focused"
                                    @click:append="show = !show"
                                    single-line
                                    outlined
                                    ></v-text-field>
                                    <label>Phone</label>
                                    <v-text-field prepend-inner-icon="mdi-phone" v-model="phone" background-color="white" type="number" :rules="phoneRules" label="Telfono" min="0" single-line outlined required></v-text-field>
                                </v-col>
                            </v-row>
                            <v-row justify="center">
                                <v-spacer></v-spacer>
                                <v-col md="12">
                                    <h2 style="text-align:center; margin-bottom:1%; color:black">Basic Info</h2>
                                    <label>Event Name:</label>
                                    <v-text-field v-model="event_names" label="ie, Sarah's birthday or Conference X" background-color="white" :rules="eventNameRules" outlined single-line require></v-text-field>
                                    <v-row>
                                        <v-col md="4">
                                            <label>Destination Country:</label>
                                            <v-select :items="countries" prepend-inner-icon="" label="ie, Argentina" background-color="white" outlined single-line></v-select>
                                        </v-col>
                                        <v-col md="4">
                                            <label>Provincia:</label>
                                            <v-select :items="event_types" prepend-inner-icon="" label="ie, Buenos Aires" background-color="white" outlined single-line></v-select>
                                        </v-col>
                                        <v-col md="4">
                                            <label>Adult</label>
                                            <v-select :items="events_adult" prepend-inner-icon="" label="are you adult?" background-color="white" outlined single-line></v-select>
                                        </v-col>
                                    </v-row>
                                    <v-row>
                                        <v-col md="4">
                                            <label>Event Category:</label>
                                            <v-select :items="event_categories" prepend-inner-icon="" label="ie, music or parties" background-color="white" outlined single-line></v-select>
                                        </v-col>
                                        <v-col md="4">
                                            <label>Participating artists:</label>
                                            <v-text-field prepend-inner-icon="" v-model="event_artist" background-color="white" :counter="10" :rules="artistRules" label="Write here each artist separated by a comma (,)" outlined required single-line></v-text-field>
                                        </v-col>
                                    </v-row>
                                    <label>Event Description:</label>
                                    <v-textarea v-model="event_description" solo label="" background-color="white"  require></v-textarea>
                    
                                    <h2 style="text-align:center; margin-bottom:1%; margin-top:10%; color:black">Date and Time</h2>
                                    <v-row>
                                        <v-col md="4">
                                            <label>Starting Date and Time:</label>
                                            <v-menu ref="start_date_menu" v-model="start_date_menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                            <template v-slot:activator="{ on }">
                                            <v-text-field v-model="start_date" label="Starting date:" prepend-icon="event" readonly v-on="on"></v-text-field>
                                            </template>
                                            <v-date-picker v-model="start_date" no-title scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                            <v-btn text color="primary" @click="$refs.start_date_menu.save(start_date)">OK</v-btn>
                                            </v-date-picker>
                                            </v-menu>
                                        </v-col>
                                        <v-col md="4" class="mt-6">
                                            <v-menu ref="start_menu" v-model="starttime" :close-on-content-click="false" :nudge-right="40" :return-value.sync="time" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                            <template v-slot:activator="{ on }">
                                            <v-text-field v-model="start_time" label="Start time:" prepend-icon="access_time" v-on="on"></v-text-field>
                                            </template>
                                            <v-time-picker v-if="starttime" v-model="start_time" full-width @click:minute="$refs.start_menu.save(start_time)"></v-time-picker>
                                            </v-menu>
                                        </v-col>
                                    </v-row>

                                    <v-row>
                                        <v-col md="4">
                                            <label>Ending Date and Time:</label>
                                            <v-menu ref="end_date_menu" v-model="end_date_menu" :close-on-content-click="false" :return-value.sync="date" transition="scale-transition" offset-y min-width="290px">
                                            <template v-slot:activator="{ on }">
                                            <v-text-field v-model="end_date" label="Ending date:" prepend-icon="event" readonly v-on="on"></v-text-field>
                                            </template>
                                            <v-date-picker v-model="end_date" no-title scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                            <v-btn text color="primary" @click="$refs.end_date_menu.save(end_date)">OK</v-btn>
                                            </v-date-picker>
                                            </v-menu>
                                        </v-col>
                                        <v-col md="4" class="mt-6">
                                            <v-menu ref="end_menu" v-model="endtime" :close-on-content-click="false" :nudge-right="40" :return-value.sync="time" transition="scale-transition" offset-y max-width="290px" min-width="290px">
                                            <template v-slot:activator="{ on }">
                                            <v-text-field v-model="end_time" label="End time:" prepend-icon="access_time" v-on="on"></v-text-field>
                                            </template>
                                            <v-time-picker v-if="endtime" v-model="end_time" full-width @click:minute="$refs.end_menu.save(endtime)"></v-time-picker>                                   
                                            </v-menu>
                                        </v-col>
                                    </v-row>
                                
                                    <h2 style="text-align:center; margin-bottom:1%; margin-top:10%; color:black">Location</h2>
                                    <label>Select area:</label>
                                    <v-select :items="event_area" label="ie, Argentina" background-color="white" outlined single-line></v-select>
                                    <label>Location address:</label>
                                    <v-text-field prepend-inner-icon="" v-model="address" background-color="white" :rules="addressRules" label="Address" outlined single-line required></v-text-field>
                                    <label>Place of event:</label>
                                    <!-- <v-text-field prepend-inner-icon="" v-model="event_place" background-color="white" :counter="10" :rules="nameRules" label="Place of event" outlined single-line required></v-text-field> -->
                                    <v-select :items="event_place" label="ie, Buenos Aires" background-color="white" outlined single-line></v-select>
                                
                                    <h2 style="text-align:center; margin-bottom:1%; margin-top:10%; color:black">Video and Image</h2>
                                    <label>Choose Event Video:</label>
                                    <v-text-field v-model="event_video" background-color="white" :rules="videoRules" label="Choose the video from Youtube" outlined single-line required></v-text-field>
                                    <label>Choose Event Image:</label>
                                    <div v-if="uploaded">
                                        <img :src="imgDataUrl" width="200px" height="200px">
                                    </div>
                                    <div v-if="!uploaded">
                                        <img src="../assets/img/producer1.png" width="200px" height="200px">
                                    </div>
                                    <!-- <v-btn @click="onPickImage">Choose Image</v-btn>
                                    <input type="file" style="display:none" ref="fileInput" accept="image/*" @change="onImagePicked"> -->
                                    <!-- <img :src="imgDataUrl"> -->
                                    <v-btn class="mt-3 ml-5" @click="toggleShow">Choose Image</v-btn>
                                    <my-upload field="img"
                                    @crop-success='cropSuccess'
                                    @crop-upload-success='cropUploadSuccess'
                                    @crop-upload-fail='cropUploadFail'
                                    v-model="uploadshow"
                                    :width="200"
                                    :height="200"
                                    url=""
                                    :params="params"
                                    :headers="headers"
                                    lang-type="en"
                                    :value.sync="uploadshow"
                                    img-format="png">
                                    </my-upload>
                                    <v-checkbox v-model="checkbox" color="black">
                                        <template v-slot:label>
                                            <div style="color:black">
                                            I have read the 
                                            <v-tooltip bottom>
                                                <template v-slot:activator="{ on }">
                                                <a href="#" @click.stop v-on="on">
                                                    <font style="color:black">terms and conditions.</font>
                                                </a>
                                                </template>
                                                Open terms and conditions
                                            </v-tooltip>
                                            </div>
                                        </template>
                                    </v-checkbox>
                                    <v-btn @click="$router.push('/event/list')">Create Event</v-btn>
                                    <v-btn @click="reset()">Reset</v-btn>
                                </v-col>
                            </v-row>
                        </v-col>
                    </v-row>
                </v-form>
            </v-col>
        </v-row>
    </v-content>
    <Footer></Footer>
</div>
</template>
<script>
import Header from './Header'
import 'babel-polyfill'
import myUpload from 'vue-image-crop-upload'
import Footer from './Footer'
export default {
    name: 'CreateEvent',
    components: {
        "Header": Header,
        'my-upload': myUpload,
        'Footer': Footer

    },
    $_veeValidate: {
        validator: 'new'
    },
    data() {
        return {
            valid: true,
            user_name: '',
            user_surname: '',
            id: '',
            nameRules: [
                v => !!v || 'Name is required',
                v => (v && v.length <= 10) || 'Name must be less than 10 characters',
            ],
            surnameRules: [
                v => !!v || 'Surname is required',
                v => (v && v.length <= 10) || 'Surname must be less than 10 characters',
            ],
            email: "",
            emailRules: [
                v => !!v || "E-mail is required",
                v => /.+@.+\..+/.test(v) || "E-mail must be valid"
            ],
            phone: '',
            phoneRules: [
                v => !!v || "Phone number is required"
            ],
            password: '',
            show: true,
            passwordRules: {
                required: value => !!value || 'Required.',
                min: v => v.length >= 8 || 'Min 8 characters',
            },
            eventNameRules: [
                v => !!v || "Event name is required"
            ],
            artistRules: [
                v => !!v || "Artist is required"
            ],
            addressRules: [
                v => !!v || "Address is required."
            ],
            // confirm_password: '',

            countries: ['Argentina', 'Chile', 'Uruguay', 'Paraguay', 'Peru', 'Ecuador', 'Colombia'],
            event_types: ['Public', 'Private'],
            events_adult: ['Yes', 'No'],
            event_names: '',
            event_categories: ['Music', 'Parties', 'Culture', 'Fairs', 'Family', 'Sports', 'Transporation', 'Tpurism', 'Club', 'Theater', 'Social Work', 'Educational', 'Other'],
            event_artist: '',
            event_description: '',
            event_area: ['Chile', 'Argentina', 'Uruguay', 'Paraguay', 'Peru', 'Colombia', 'Ecuador', 'Bolivia'],
            address: '',
            event_place: [],
            event_video: '',
            videoRules: [
                v => !!v || "Video url is required"
            ],
            checkbox: false,
            date: null,
            time: null,
            starttime: false,
            start_time: '',
            endtime: false,
            end_time: '',
            start_date: new Date().toISOString().substr(0,10),
            start_date_menu:false,
            end_date: new Date().toISOString().substr(0,10),
            end_date_menu:false,
            uploaded: false,
            //image upload
            // imgshow: false,
            uploadshow: false,
            params: {
				token: '123456798',
				name: 'avatar'
			},
			headers: {
				smail: '*_~'
			},
			imgDataUrl: '' // the datebase64 url of created image
        }
    },

    methods: {
        reset() {
            this.$refs.form.reset();
        },
        submit() {
            this.$validator.validateAll()
                .then(result => {
                    console.log(result)
                })
        },
        // onPickImage() {
        //   this.$refs.fileInput.click();
        // },
        // onImagePicked(event) {
        //     const files = event.target.files;
        //     let filename = files[0].name;
        //     if (filename.lastIndexOf('.') <= 0) {
        //         return alert('Please a valid image!')
        //     } 
        //     const fileReader = new FileReader();
        //     fileReader.addEventListener('load', () => {
        //         this.imageUrl = fileReader.result
        //     });
        //     fileReader.readAsDataURL(files[0]);
        //     this.uploaded = true;
        //     this.image = files[0];
        // },
        // Image crop and upload
        toggleShow() {
				this.uploadshow = !this.uploadshow;
        },
        cropSuccess(imgDataUrl, field){
				console.log('-------- crop success --------');
                this.imgDataUrl = imgDataUrl;
                this.uploaded = !(this.uploaded);
        },
        cropUploadSuccess(jsonData, field){
				console.log('-------- upload success --------');
				console.log(jsonData);
				console.log('field: ' + field);
        },
        cropUploadFail(status, field){
				console.log('-------- upload fail --------');
				console.log(status);
				console.log('field: ' + field);
		},
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#tellus {
    background-color: white;
    background-size: 100%, 100%;
}
#createevent {
    background-color: #e8edea;
    background-size: 100%, 100%;
}
.titlebg {
    background-color: #fdfffe;
    background-size: 100%, 100%;
}
</style>



