<template>
<div>
    <Header></Header>
    <v-content class="home" id="searchhome">
        <v-container>
            <div id="title" style="text-align:center; margin-top:70px" >
                <div><h1 class="white--text">Encuentra el evento idea!</h1></div>
                <h3 class="white--text mt-5 mb-5">Conciertos, fiestas, teatro y mucho mas.</h3>
                <v-row justify="center">
                    <v-col md="5">
                        <div style="margin-bottom:200px" class="searchhome">
                            <v-text-field v-model="search" background-color="transparent" label="search" single-line outlined class="hometextfield"></v-text-field>
                            <!-- <v-icon class="white--text">place</v-icon>
                            <span class="white--text font-weight-bold">Argentina </span>
                            <span class="white--text">Cambiar localization</span> -->
                            <!-- <v-select :items="countries" prepend-inner-icon="place" label="ie, Argentina" background-color="transparent" class="homeselect" outlined single-line></v-select> -->
                            <v-dialog v-model="dialog" persistent max-width="600px">
                                <template v-slot:activator="{ on }">
                                    <v-btn text v-on="on" v-model="country_btn">
                                        <v-icon class="white--text">place</v-icon>
                                        <span class="white--text font-weight-bold text-capitalize">{{country_btn}} </span>
                                        <span class="white--text caption text-capitalize ml-3">Cambiar localization</span>
                                    </v-btn>
                                </template>
                                <v-card>
                                    <v-card-text>
                                        
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-container>
                                            <v-row>
                                                <v-col md="4" class="mb-5"><img src="../assets/img/planet5-512.png" width="150px" height="150px"></v-col>
                                                <v-col md="8" class="mt-5">
                                                    <v-select :items="countries" v-model="country_select" prepend-inner-icon="place" label="ie, Argentina" background-color="transparent" class="homeselect" outlined single-line></v-select>
                                                    <v-btn color="orange" block @click="change_Country()">CHANGE</v-btn>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                    </v-card-actions>
                                </v-card>
                                </v-dialog>            
                        </div>
                        </v-col>
                </v-row>
            </div>
        </v-container>
    </v-content>
</div>    
</template>
<script>
import Header from './Header'
export default {
    components: {
        "Header": Header
    },
    data() {
        return {
            search: '',
            countries: ['Argentina', 'Peru'],
            country_btn: 'Argentina',
            country_select: '',
            dialog: false,
        }
    },
    methods: {
        change_Country() {
            this.dialog = false;
            this.country_btn = this.country_select;
        },
    },
}
</script>
<style lang="scss" scoped>
#searchhome {
    // background-image: -webkit-linear-gradient( -29deg, rgb(193, 15, 65) 0%, rgb(35, 59, 136) 100%);
    background-image: url("../assets/img/hero-1-img.png");
    background-size: 100% 750px;
    // height: 750px;
}
.homeselect {
    fieldset {
        border-color: transparent;
    }

}
</style>


